  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #30475E;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  width: 100%;
}
@media (min-width: 768px) {
  .contenido-centrado {
    max-width: 800px;
    margin: auto;
  }
}

/**Layout**/
.webp .inicio {
  background-image: url("/build/img/moji-sena.webp");
  background-repeat: no-repeat;
}

.no-webp .inicio {
  background-image: url("/build/img/moji-sena.jpg");
  background-repeat: no-repeat;
}

.header {
  background-position: center right;
  min-height: 40rem;
}
@media (min-width: 1024px) {
  .header.inicio {
    max-width: 70%;
    margin: auto;
    background-size: cover;
    background-position: center center;
  }
}

.header-contenido {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 1rem;
  min-height: 40rem;
  background-color: rgba(51, 51, 51, 0.5803921569);
}
.header-contenido h1, .header-contenido a {
  color: #F05454;
  font-weight: 900;
  margin-top: 0;
}
@media (min-width: 1024px) {
  .header-contenido {
    align-items: flex-start;
  }
  .header-contenido a {
    display: inline-block;
    padding-right: 1rem;
    font-size: 2rem;
    margin-top: 3rem;
  }
}

.navegacion {
  padding-right: 1rem;
}

.navegacion-contenido {
  display: flex;
  flex-direction: column;
  column-gap: 1rem;
  text-align: end;
}
@media (min-width: 1024px) {
  .navegacion-contenido {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.informacion-contenido h1 {
  color: #F05454;
  font-size: 3.8rem;
  line-height: 1.5;
  font-weight: 900;
}
.informacion-contenido p {
  color: #DDDDDD;
  font-size: 1.2rem;
  line-height: 1.5;
  text-align: justify;
  text-indent: 6rem;
}
@media (min-width: 768px) {
  .informacion-contenido p {
    font-size: 2rem;
  }
}

.footer {
  background-color: #222831;
}
.footer p {
  font-size: 1rem;
  text-align: center;
  padding: 2rem 0;
  margin: 0;
  color: #DDDDDD;
}
@media (min-width: 768px) {
  .footer p {
    font-size: 1.8rem;
  }
}

.redes {
  margin: 2rem 0 2rem 0;
}
.redes .red-contenido {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .redes {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 20rem;
  }
}

.red-contenido {
  text-align: center;
}
.red-contenido h2 {
  margin: 0;
}

.red {
  background-color: #30475E;
  border-radius: 0 0 1rem 1rem;
  margin-top: 3rem;
}

.titulo {
  background-color: #222831;
  color: #DDDDDD;
  font-size: 2.5rem;
  border-radius: 1rem 1rem 0 0;
  padding: 1rem;
}

fieldset {
  max-width: 100%;
  border: 1px solid #222831;
  border-radius: 2rem 0;
}

.formulario {
  margin-bottom: 2rem;
}
.formulario legend {
  color: #DDDDDD;
  font-size: 2.5rem;
}
.formulario label {
  display: block;
  color: #DDDDDD;
  font-size: 2;
}
.formulario input, .formulario textarea {
  width: 100%;
  border-radius: 1rem;
  padding: 1.5rem 1rem;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .formulario input, .formulario textarea {
    width: 100%;
  }
}
.formulario textarea {
  height: fit-content;
}
@media (min-width: 768px) {
  .formulario textarea {
    min-height: 30rem;
    resize: none;
  }
}

.boton-contenido {
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .boton-contenido {
    justify-content: flex-end;
  }
}

.boton {
  background-color: #F05454;
  color: #DDDDDD;
  font-weight: 900;
  font-size: 1.8rem;
  text-transform: uppercase;
  border: none;
  margin-top: 1rem;
  padding: 1rem 5rem;
  width: 100%;
}
.boton:hover {
  background-color: #ee3d3d;
  color: #222831;
  cursor: pointer;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}

h1.resultado-header {
  color: #FFFFFF;
  font-size: 3.8rem;
  font-weight: 700;
}

.resultados {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}
.resultados .resultado {
  margin-top: 1rem;
}
@media (min-width: 480px) {
  .resultados {
    grid-template-columns: 1fr 1fr;
  }
  .resultados .resultado {
    margin-top: 2rem;
  }
}
@media (min-width: 768px) {
  .resultados {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.resultados img {
  max-width: 25rem;
  max-height: 15rem;
}
@media (min-width: 480px) {
  .resultados img {
    max-width: 40rem;
    max-height: 25rem;
  }
}

.resultado p {
  color: #FFFFFF;
  font-size: 3.5rem;
  margin: 1rem 0;
}
.resultado p.descripcion {
  font-size: 2.1rem;
}

.boton-primario {
  background-color: #71B100;
  color: #DDDDDD;
  font-weight: 900;
  font-size: 1.8rem;
  text-transform: uppercase;
  border: none;
  margin-top: 1rem;
  padding: 1rem 5rem;
  text-align: center;
}
.boton-primario:hover {
  background-color: #619800;
  color: #222831;
  cursor: pointer;
}

.botones-formulario {
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .botones-formulario {
    flex-direction: row;
    justify-content: space-around;
  }
}