fieldset {
    max-width: 100%;
    border: 1px solid $secundario;
    border-radius: 2rem 0;

}

.formulario {

    margin-bottom: 2rem;
    legend {
        color: $primario;
        font-size: 2.5rem;
    }

    label {
        display: block;
        color: $primario;
        font-size: 2;

    }

    input, textarea {

        width: 100%;
        border-radius: 1rem;
        padding: 1.5rem 1rem;
        font-size: 1.5rem;

        @include tablet {
            width: 100%;
        }
        

    }

    textarea {

        height: fit-content;
        
        @include tablet {
            min-height: 30rem;
            resize: none;

        }
        

    }

}


.boton-contenido {

    display: flex;
    justify-content: center;

    @include tablet {
        justify-content: flex-end;
    }
    
}

.boton {
    @include boton($melon);
    width: 100%;
    @include tablet {
        width: auto;

    }


}