.redes {

    margin: 2rem 0 2rem 0;

    .red-contenido {
        margin-top: 2rem;

    }

    @include tablet {
        @include grid(3, 2rem);
        margin-bottom: 20rem;

    }

}

.red-contenido {
    text-align: center;
    
    h2 {
        margin: 0;
    }
}

.red {
    background-color: $azulCartagena;
    border-radius: 0 0 1rem 1rem;
    margin-top: 3rem;

}


.titulo {
    background-color: $secundario;
    color: $primario;
    font-size: 2.5rem;
    border-radius: 1rem 1rem 0 0;
    padding: 1rem;

}