h1 {
  &.resultado-header {
      color: $blanco;
      font-size: 3.8rem;
      font-weight: $bold;
  }

}
.resultados {

    display: grid;
    grid-template-columns: 1fr;
    gap: 2rem;


    .resultado {
        margin-top: 1rem;
    }

    @include telefono {
        grid-template-columns: 1fr 1fr;

        .resultado {
            margin-top: 2rem;
        }

    }

    @include tablet {
        grid-template-columns:1fr 1fr 1fr;

    }

    img {


        max-width: 25rem;
        max-height: 15rem ;

        @include telefono {
            max-width: 40rem;
            max-height: 25rem;
            
        }

    }


}

.resultado {

    p {
        color: $blanco;
        font-size: 3.5rem;
        margin: 1rem 0;

        &.descripcion {
            font-size: 2.1rem;
        }


    }

}


.boton-primario {
    @include boton($verde);
    text-align: center;
    
}

.botones-formulario {
    display: flex;
    flex-direction: column;
    margin-top: 2rem;

    @include tablet {
        
        flex-direction: row;
        justify-content: space-around;

    }
    

}