.webp .inicio {
    
    background-image: url('/build/img/moji-sena.webp');
    background-repeat: no-repeat;

}

.no-webp .inicio {
    
    background-image: url('/build/img/moji-sena.jpg');
    background-repeat: no-repeat;

}

.header {
    background-position: center right;
    min-height: 40rem;

    &.inicio {
        @include desktop {
            
            max-width: 70%;
            margin: auto;
            background-size: cover;
            background-position: center center;
        }
    }

}

.header-contenido {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 1rem;
    min-height: 40rem;
    background-color: $grisOscuro;

    h1, a {
        color: $melon;
        font-weight: $black;
        margin-top: 0;
    }

    @include desktop{
        
        align-items: flex-start;        
        
        a {
            display: inline-block;
            padding-right: 1rem;
            font-size: 2rem;
            margin-top: 3rem;
        }

    }



}

