/** Media Queries **/
@mixin telefono {
    @media (min-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin boton($bgColor) {
    background-color: $bgColor;
    color: $primario;
    font-weight: $black;
    font-size: 1.8rem;
    text-transform: uppercase;
    border: none;
    margin-top: 1rem;
    padding: 1rem 5rem;
    &:hover {
        background-color: darken($bgColor, 5);
        color: $secundario;
        cursor: pointer;
    }

}